<template>
  <input
    class="input__text"
    type="text"
    :value="modelValue.value"
    :placeholder="getPlaceholder"
    v-focus-class
    @input="updateValue"
    @focus="updateValue"
    :disabled="props.disabled"
  />
</template>

<script lang="ts" setup>
import { defineProps, computed, defineEmits, PropType } from "vue";
import { vFocusClass } from "@/directives/FocusDirective";
import { InputText } from "@/interfaces/InputTextInterface";

const props = defineProps({
  modelValue: {
    type: Object as PropType<InputText>,
    required: true,
  },
  propertyName: {
    type: String,
    required: false,
  },
  disabled: {
    type: Boolean,
    required: false,
  },
});

const emit = defineEmits(["update:modelValue", "input", "update:value"]);

const getPlaceholder = computed((): string => {
  return props.modelValue.placeholder || "Текст";
});

const updateValue = (event: Event) => {
  const target = event.target as HTMLInputElement;
  const newValue = target.value;

  emit("update:modelValue", {
    ...props.modelValue,
    value: newValue,
  });

  emit("update:value", newValue);

  emit("input", {
    eventValue: newValue,
    eventType: props.modelValue.type,
    eventName: props.propertyName,
    eventValid: newValue.length > 0,
  });
};
</script>

<style scoped lang="scss">
@import url(../../styles/variable.css);

input[type="text"] {
  caret-color: var(--yellow);
}
</style>
