<template>
  <input
    class="input__phone"
    v-if="getTypeInput === 'phone' || getTypeInput === 'whts'"
    type="phone"
    v-phone-mask
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.modelValue!.disabled"
  />

  <input
    class="input__card"
    v-else-if="getTypeInput === 'card'"
    type="text"
    v-card-number
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.modelValue!.disabled"
  />

  <input
    class="input__date"
    v-else-if="getTypeInput === 'date'"
    type="text"
    v-date
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.modelValue!.disabled"
  />

  <input
    class="input__time"
    v-else-if="getTypeInput === 'time'"
    type="text"
    v-time
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.modelValue!.disabled"
  />

  <input
    class="input__number"
    v-else-if="!getTypeInput"
    type="number"
    v-focus-class
    v-model="numberValue"
    :placeholder="getPlaceholderInput"
    @input="setNewValue"
    :disabled="props.modelValue!.disabled"
  />
</template>

<script lang="ts" setup>
import { vPhoneMask } from "@/directives/ValidationPhoneDirective";
import { vFocusClass } from "@/directives/FocusDirective";
import { vCardNumber } from "@/directives/CardNumberDirective";
import { vDate } from "@/directives/DateDirective";
import {
  defineProps,
  computed,
  ref,
  defineEmits,
  onMounted,
  watch,
  PropType,
} from "vue";
import vTime from "@/directives/TimeDirective";
import { InputNumber } from "@/interfaces/InputNumberInterface";

const emit = defineEmits(["input", "update:value"]);

const props = defineProps({
  modelValue: {
    type: Object as PropType<InputNumber>,
  },
});

const numberValue = ref<string | number>("");

const MAX_LENGTH_PHONE = 11;

const getTypeInput = computed(() => {
  if (props.modelValue) {
    return props.modelValue.type;
  }
});

const getPlaceholderInput = computed(() => {
  if (props.modelValue) {
    return props.modelValue.placeholder || "число";
  }
});

function applyPhoneMask(value: string): string {
  const matrix = "+7 (___) ___-__-__";
  const def = matrix.replace(/\D/g, "");
  const val = value.replace(/\D/g, "");

  let i = 0;
  const newValue = matrix.replace(/[_\d]/g, (a) => {
    return i < val.length ? val.charAt(i++) || def.charAt(i) : a;
  });

  i = newValue.indexOf("_");
  if (i !== -1) {
    i < 5 && (i = 3);
    return newValue.slice(0, i);
  }

  return newValue;
}

const setNewValue = () => {
  const valueAsString = String(numberValue.value);

  if (getTypeInput.value === "phone" || getTypeInput.value === "whts") {
    const digits = valueAsString.replace(/\D/g, "").slice(0, MAX_LENGTH_PHONE);
    emit("update:value", digits);
    emit("input", digits);
  } else {
    emit("update:value", valueAsString);
    emit("input", valueAsString);
  }
};

onMounted(() => {
  if (props.modelValue && props.modelValue !== null) {
    if (props.modelValue.type === "phone" || props.modelValue.type === "whts") {
      const maskedValue = applyPhoneMask(String(props.modelValue.value.value));
      numberValue.value = maskedValue;
    } else {
      numberValue.value = String(props.modelValue.value.value);
    }
  }
});

watch(
  () => props.modelValue,
  (newValue) => {
    if (newValue !== null) {
      const maskedValue = applyPhoneMask(String(newValue));
      numberValue.value = maskedValue;
    }
  }
);
</script>

<style scoped lang="scss">
@import url(../../styles/variable.css);

input[type="text"] {
  caret-color: var(--yellow);
}
input:disabled {
  color: var(--light-gray);
}
</style>
